<script>
// import Datepicker from 'vue3-datepicker'
// const picked = ref(new Date())
import { useRouter } from 'vue-router';
import FooterVue from '../FooterVue.vue';
import HeaderVue from '../HeaderVue.vue';
import { reactive } from 'vue'
import axios from 'axios';


export default {
  components: { HeaderVue, FooterVue },
  setup () {
    const state = reactive({
      id: '',
      pw: '',
      pw1: '',
      mname: '',
      cpname: '',
      phone: '',
      address: '',
      postcode: '',
      detailAddress: '',
      role : 'COMPANY',
      email: '',
      number: '',
      extraAddress: '',
      birthcompany: '',
      desc: '',
      imageUrl : require('../../assets/img/profile_sample.png'),
      imageFile : null,
      show1: false,
      show2: false,

      idRules: [
        v => !!v || '  ',
        v => /^[a-zA-Z0-9]*$/.test(v) || ' ,   ',
        v => v.length >= 6 || '6  ',
      ],

      numberRules: [
        v => !!v || '  ',
        v => v.length >= 10 || '10  ',
        v => v.length <= 10 || '10  ',
      ],
      
      birthcompanyRules: [
        v => v.length >= 8 || '8  ',
        v => v.length <= 8 || '8  ',
      ],

      nameRules: [
        v => !!v || '  ',
        v => !/[~!@#$%^&*()_+|<>?:{}]/.test(v) || '    '
      ],

      nicknameRules: [
        v => !!v || '  ',
      ],

      emailRules: [
        v => !!v || '  ',
        v => /.+@.+/.test(v) || '  ',
      ],

      birthRules: [
        v => !!v || '  ',
        v => v.length >= 8 || '8  ',
        v => v.length <= 8 || '8  ',
      ],

      phoneRules: [
        v => !!v || '  ',
        v => v.length >= 8 || '',
        v => v.length <= 11 || '',
      ],

      validnumberRules: [
        v => !!v || '  ',
        v => v.length >= 6 || '',
        v => v.length <= 6 || '',
      ],
    })
    const router = useRouter();


    const handleImage = (e) => {
      if(e.target.files[0]){
          state.imageUrl = URL.createObjectURL(e.target.files[0]);
          state.imageFile = e.target.files[0];
      }
      else{
          state.imageUrl = require('../../assets/img/profile_sample.png');
          state.imageFile = null;
      }
    }

    const handleJoin2 = async() => {
      const url = `/cluver/member/cpjoin.json`;
      const headers = {"Content-Type":"multipart/form-data"};
      const body = new FormData;
        body.append("mcno",    state.number);
        body.append("mcdesc",  state.desc);
        body.append("mcname",  state.cpname);
        body.append("mcbirth", state.birthcompany);
        body.append("mcrole",   state.role);
        body.append("member",  state.id);
      const response = await axios.post(url,body,{headers});
      console.log(response.data);
      if(response.data.status === 200){
        alert('')
        router.push({path : 'login'});
      }
    }

    const handleJoin = async() => {
      if(state.chk1 === false) {
        alert(' 15    ')
        return false;
      }

      else if(state.chk2 === false) {
        alert('  ')
        return false;
      }

      else if(state.chk3 === false) {
        alert('  ')
        return false;
      }

      else if(state.id === '' || state.id.length < 6) {
        alert(' 6  ')
        return false;
      }

      else if(state.pw === '') {
        alert(' ')
        return false;
      }

      else if(state.pw1 === '') {
        alert('  ')
        return false;
      }

      else if(state.number === '' || state.number.length < 10 || state.number.length > 10) {
        alert(' 10 ')
        return false;
      }      

      else if(state.mname === '') {
        alert(' ')
        return false;
      }

      else if(state.cpname === '') {
        alert(' ')
        return false;
      }

      else if(state.phone === '') {
        alert(' ')
        return false;
      }

      const url = `/cluver/member/join.json`;
      const headers = {"Content-Type":"multipart/form-data"};

      const body = new FormData;
        body.append("mid",      state.id);
        body.append("mpw",      state.pw);
        body.append("mname",    state.mname);
        body.append("mphone",   state.phone);
        body.append("maddress", state.address);
        body.append("detailaddress" ,state.extraAddress);
        body.append("memail",   state.email);
        body.append("file",     state.imageFile);
      const response = await axios.post(url,body,{headers});
      console.log(response.data);
      if(response.data.status === 200){
        handleJoin2();
      }
    }

    const post = () => {
      new window.daum.Postcode({
        oncomplete: (data) => {
          if (state.extraAddress !== "") {
            state.extraAddress = "";
          }
          if (data.userSelectedType === "R") {
            //     
            state.address = data.roadAddress;

          } else {
            //     (J)
            state.address = data.jibunAddress;
          }
 
          //       .
          if (data.userSelectedType === "R") {
            //    . ( )
            //     "//" .
            if (data.bname !== "" && /[||]$/g.test(data.bname)) {
              state.extraAddress += data.bname;
            }
            //  ,   .
            if (data.buildingName !== "" && data.apartment === "Y") {
              state.extraAddress +=
                state.extraAddress !== ""
                  ? `, ${data.buildingName}`
                  : data.buildingName;
            }
            //    ,     .
            if (state.extraAddress !== "") {
              state.extraAddress = `(${state.extraAddress})`;
            }
          } else {
            state.extraAddress = "";
          }
          //  .
          state.postcode = data.zonecode;
        },
      }).open();
    }

    return { post, handleJoin, state, handleImage }
  },
}
</script>

